.item {
  display: flex;
  align-items: center;
  padding: 5px;
}

.item.mine {
  flex-direction: row-reverse;
}

.messageText {
  margin: 0;
  width: 60%;
  padding: 15px;
  border-radius: 10px;
}

.item .messageText {
  background-color: rgba(0, 38, 255, 0.555);
}

.item.mine .messageText{
  background-color: rgba(94, 255, 0, 0.555);
}

.avatarBox {
  width: 25px;
  height: 25px;
  overflow: hidden;
  border-radius: 100%;
}

.item .avatarBox{
  margin-right: 5px;
}

.item.mine .avatarBox{
  margin-left: 5px;
}

.avatarBox img {
  height: 100%;
  width: 100%;
}
