.item {
  display: flex;
  align-items: center;
  padding: 5px;
}

.link {
  color: black;
}

.link:hover {
  color: yellow;
}

.activeLink {
  font-weight: bold;
  color: chartreuse;
}

.avatarBox {
  width: 25px;
  height: 25px;
  margin-right: 10px;
  border-radius: 100%;
  overflow: hidden;
}

.avatarBox img {
  width: 100%;
  height: 100%;
}
